  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Press Start 2P", sans-serif;
  background-color: cornflowerblue;
}

fieldset {
  border: none;
  padding: 0;
  margin-bottom: 1rem;
}

legend {
  font-size: 0.75rem;
  color: #222;
  margin-bottom: 0.75rem;
  padding: 0;
}

.header {
  display: flex;
  justify-content: center;
  margin: 25px;
}

.title {
  border-radius: 15px;
  border: 4px solid white;
  border-style: double;
  padding: 15px;
}

/* PREGAME-FORM */

.pregame-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.pregame-container {
  padding: 2rem;
  border-radius: 12px;
  background: white;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  text-align: center;
  max-width: 500px;
  width: 100%;
}

/* HEADER */

.pregame-header h3 {
  margin-bottom: 1.5rem;
  font-size: 1rem;
  color: #333;
}

/* FORM ELEMENTS */

.form-group {
  display: flex;
  flex-direction: column;
  text-align: left;
}

label {
  font-size: 0.75rem;
  color: #222;
}

/* TOGGLE GROUP */

.toggle-group {
  display: flex;
  justify-content: start;
  gap: 0.5rem;
}

.toggle-group input[type="radio"] {
  display: none;
}

.toggle-group label {
  padding: 0.5rem 1rem;
  border: 2px solid #007bff;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  flex: 1;
  text-align: center;
}

.toggle-group input[type="radio"]:checked+label {
  background-color: #007bff;
  color: white;
}

/* BUTTONS */

.form-actions {
  display: flex;
  justify-content: center;
}

.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 8px;
  background-color: #007bff;
  color: white;
  font-family: inherit;
  font-size: 0.7rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
  display: block;
  margin: 10px auto;
}

.new-game-btn {
  font-size: 1rem;
}

.btn:hover {
  background-color: #0056b3;
}

/* BOARD */

.game-screen {
  display: none;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  min-height: 100vh;
}

.game-screen.active {
  display: flex;
}

.boards-container {
  display: flex;
  gap: 3rem;
  justify-content: center;
  flex-wrap: wrap;
}

.board-section {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.board-title {
  font-size: 0.7rem;
  color: #333;
  margin-bottom: 1rem;
  text-align: center;
}

.board {
  display: grid;
  grid-template-columns: repeat(10, 30px);
  gap: 2px;
  background-color: #ddd;
  padding: 5px;
}

.cell {
  width: 30px;
  height: 30px;
  background-color: #87ceeb;
  border: 1px solid #0099cc;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.cell:hover {
  background-color: #5ba3d0;
}

.cell.ship {
  background-color: #666;
}

.cell.hit {
  background-color: #ff4444;
}

.cell.miss {
  background-color: #ffff99;
}

/* SHIP GUAGE */

.ship-gauge {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.ship-circle {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background: #87ceeb;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000;
  font-weight: bold;
  border: 2px solid #000;
  transition: all 0.3s ease;
}

.ship-circle.sunk {
  background: #ff4444;
  color: white;
  border-color: #880000;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  align-items: center;
  justify-content: center;
}

.modal.active {
  display: flex;
}

.modal-content {
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  min-width: 300px;
}

.modal-content h2 {
  margin: 0 0 20px 0;
  font-size: 2em;
}

/* PLACE SHIPS SCREEN */

.placement-screen {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.placement-container {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  text-align: center;
}

.placement-container h2 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.placement-container p {
  font-size: 0.7rem;
  margin-bottom: 1rem;
  color: #333;
}

#rotateShipBtn {
  margin-bottom: 1rem;
}

.cell.preview-valid {
  background-color: #90EE90 !important;
  cursor: pointer;
}

.cell.preview-invalid {
  background-color: #FFB6C6 !important;
  cursor: not-allowed;
}

#placementBoard {
  width: fit-content;
  margin: 20px auto;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAEA;EACE,sBAAsB;EACtB,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,yCAAyC;EACzC,gCAAgC;AAClC;;AAEA;EACE,YAAY;EACZ,UAAU;EACV,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,sBAAsB;EACtB,UAAU;AACZ;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,YAAY;AACd;;AAEA;EACE,mBAAmB;EACnB,uBAAuB;EACvB,oBAAoB;EACpB,aAAa;AACf;;AAEA,iBAAiB;;AAEjB;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,aAAa;AACf;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,iBAAiB;EACjB,0CAA0C;EAC1C,kBAAkB;EAClB,gBAAgB;EAChB,WAAW;AACb;;AAEA,WAAW;;AAEX;EACE,qBAAqB;EACrB,eAAe;EACf,WAAW;AACb;;AAEA,kBAAkB;;AAElB;EACE,aAAa;EACb,sBAAsB;EACtB,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,WAAW;AACb;;AAEA,iBAAiB;;AAEjB;EACE,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,oBAAoB;EACpB,yBAAyB;EACzB,kBAAkB;EAClB,eAAe;EACf,yBAAyB;EACzB,OAAO;EACP,kBAAkB;AACpB;;AAEA;EACE,yBAAyB;EACzB,YAAY;AACd;;AAEA,YAAY;;AAEZ;EACE,aAAa;EACb,uBAAuB;AACzB;;AAEA;EACE,uBAAuB;EACvB,YAAY;EACZ,kBAAkB;EAClB,yBAAyB;EACzB,YAAY;EACZ,oBAAoB;EACpB,iBAAiB;EACjB,eAAe;EACf,sCAAsC;EACtC,cAAc;EACd,iBAAiB;AACnB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA,UAAU;;AAEV;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,aAAa;EACb,iBAAiB;AACnB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;EACb,SAAS;EACT,uBAAuB;EACvB,eAAe;AACjB;;AAEA;EACE,iBAAiB;EACjB,eAAe;EACf,mBAAmB;EACnB,0CAA0C;AAC5C;;AAEA;EACE,iBAAiB;EACjB,WAAW;EACX,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,uCAAuC;EACvC,QAAQ;EACR,sBAAsB;EACtB,YAAY;AACd;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,yBAAyB;EACzB,yBAAyB;EACzB,eAAe;EACf,sCAAsC;AACxC;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA,eAAe;;AAEf;EACE,aAAa;EACb,SAAS;EACT,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,mBAAmB;EACnB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,WAAW;EACX,iBAAiB;EACjB,sBAAsB;EACtB,yBAAyB;AAC3B;;AAEA;EACE,mBAAmB;EACnB,YAAY;EACZ,qBAAqB;AACvB;;AAEA;EACE,aAAa;EACb,eAAe;EACf,aAAa;EACb,OAAO;EACP,MAAM;EACN,WAAW;EACX,YAAY;EACZ,oCAAoC;EACpC,mBAAmB;EACnB,u